export default {
  props: {
    /**
     * 
     * primary: 
     * warn
     * default
     */
    type: {
      type: String,
      default: 'primary',
      required: false
    },

    /**
     * disabledtrue
     */
    disabled: {
      type: Boolean,
      default: false,
      required: false
    },

    /**
     * 
     */
    mini: {
      type: Boolean,
      default: false,
      required: false
    },

    /**
     * plainplain
     * primarydefaultplain
     */
    plain: {
      type: Boolean,
      default: false,
      required: false
    }
  },

  computed: {
    typeClass() {
      return `weui_btn${this.plain ? '_plain' : ''}_${this.type}`;
    }
  }
}


