import {Actionsheet, Button} from 'vue-weui';

export default {
  data() {
    return {
      actionsheetShow: false,
      actionSheetMenus: {
        menu1: '',
        menu2: '',
        menu3: '',
        menu4: ''
      },
      actionSheetActions: {
        action1: ''
      }
    }
  },

  methods: {
    showActionsheet() {
      this.actionsheetShow = true;
    },

    handleMenuClick(key) {
      alert(`${key}`);
    },

    handleActionClick(key) {
      alert('Actionsheet');
    }
  },

  components: {
    Actionsheet,
    'weui-button': Button
  }
}


