import CellBody from './cell-body.vue';
import CellFooter from './cell-footer.vue';
export default {
  props: {
    /**
     * radio inputid
     */
    id: {
      type: String,
      required: false
    },

    /**
     * radio inputname
     */
    name: {
      type: String,
      required: false
    },

    /**
     * radio inputvalue
     */
    value: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: String,
      required: true
    },

    /**
     * radio inputv-model
     */
    picked: {
      type: String,
      required: true,
      twoWay: true
    }
  },

  components: {
    CellBody,
    CellFooter
  }
}


