import CellHeader from './cell-header.vue';
import CellBody from './cell-body.vue';

export default {
  props: {
    /**
     * checkbox inputid
     */
    id: {
      type: String,
      required: false
    },

    /**
     * checkbox inputname
     */
    name: {
      type: String,
      required: false
    },

    /**
     * checkbox inputvalue
     */
    value: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: String,
      required: true
    },

    /**
     * checkbox inputv-model
     */
    checked: {
      type: null,
      required: true,
      twoWay: true
    }
  },

  components: {
    CellHeader,
    CellBody
  }
}


