export default {
  props: {
    /**
     * 0100
     */
    progress: {
      type: Number,
      required: true,
      validator(value) {
        return value >= 0 && value <= 100;
      },
      default: 0
    },

    /**
     * weui-progress-cancel
     */
    hasCancelButton: {
      type: Boolean,
      required: false,
      default: true
    }
  },

  methods: {
    dispatchEvent(event) {
      this.$dispatch(event);
    }
  }
}


