export default {
  props: {
    /**
     * Cells
     * access: 
     * form: 
     * radio: 
     * checkbox: 
     */
    type: {
      type: String,
      default: '',
      required: false
    }
  },
  
  computed: {
    typeClass() {
      return this.type ? `weui_cells_${this.type}` : '';
    }
  }
};


