import CellHeader from './cell-header.vue';
import CellBody from './cell-body.vue';
import CellFooter from './cell-footer.vue';
import CellInput from './cell-input.vue';
import CellTextarea from './cell-textarea.vue';

export default {
  props: {
    /**
     * 
     * text: 
     * textarea
     */
    type: {
      type: String,
      required: false,
      default: 'text'
    },

    /**
     * id
     * @type {Object}
     */
    id: {
      type: String,
      required: false
    },

    /**
     * name
     */
    name: {
      type: String,
      required: false
    },

    /**
     * placeholder
     */
    placeholder: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: String,
      required: false
    },

    /**
     * inputtextareav-model
     */
    value: {
      type: String,
      required: true,
      twoWay: true
    },

    /**
     * url
     * @type {Object}
     */
    vcode: {
      type: String,
      required: false
    },

    /**
     * 
     * true
     */
    warn: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  components: {
    CellHeader,
    CellBody,
    CellFooter,
    CellInput,
    CellTextarea
  }
}


