import CellBody from './cell-body.vue';
import CellFooter from './cell-footer.vue';

export default {
  props: {
    /**
     * checkbox inputid
     */
    id: {
      type: String,
      required: false
    },

    /**
     * checkbox inputname
     */
    name: {
      type: String,
      required: false
    },

    /**
     * checkbox inputvalue
     */
    value: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: String,
      required: true
    },

    /**
     * checkbox
     * checkbox inputv-model
     */
    on: {
      type: null,
      required: true,
      twoWay: true
    }
  },

  components: {
    CellBody,
    CellFooter
  }
}


