export default {
  props: {
    /**
     * 
     * alert: 
     * confirm: 
     */
    type: {
      type: String,
      required: false,
      default: 'alert'
    },

    /**
     * 
     */
    title: {
      type: String,
      required: true
    },

    /**
     * 
     */
    confirmButton: {
      type: String,
      required: false,
      default: ''
    },

    /**
     * 
     */
    cancelButton: {
      type: String,
      required: false,
      default: ''
    }

  },

  methods: {
    dispathEventAndClose(event) {
      this.$dispatch(event);
    }
  }
}


